// main page elements
body {
	font-size: 14px;
	// color: $dark
	// font-family: '', sans-serif
	font-weight: 400;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1570px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.vseoboknax {
	background: #314A60;
	border-radius: 5px;
	display: flex;
	justify-content: space-between;
	.vseoboknax_media {
		margin-top: 93px;
		p {
			font-weight: bold;
			font-size: 14px;
			line-height: 150%;
			color: #FFFFFF; }
		a {
			margin-top: 20px;
			margin-right: 25px;
			margin-bottom: 39px; } }
	.vseoboknax_text {
		padding-left: 88px;
		padding-top: 33px;
		h3 {
			font-family: Gilroy;
			font-style: normal;
			font-weight: 800;
			font-size: 18px;
			line-height: 130%;
			text-transform: uppercase;
			color: #BBD4E4; }
		h1 {
			font-family: Gilroy;
			font-style: normal;
			font-weight: bold;
			font-size: 34px;
			line-height: 130%;
			color: #FFFFFF; }
		p {
			font-weight: bold;
			font-size: 14px;
			line-height: 150%;
			color: #FFFFFF; } } }
.slider_fourblocks {
	margin-top: 20px;
	display: flex;
	.slider {
		width: 100%;
		min-width: 1173px;
		border-radius: 10px; }
	.fourblocks {
		margin-left: 27px;
		a {
			width: 100%;
			background: #E0EDF5;
			border-radius: 5px;
			margin-bottom: 18px;
			div {
				display: flex;
				p {
					display: flex;
					align-items: center;
					margin-left: 22px;
					font-style: normal;
					font-weight: bold;
					font-size: 18px;
					line-height: 130%;
					color: #314A60; } } } } }
